.navbar{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: fixed;
    right: 0;
    padding-right: 4%;
    bottom: 10%;
    background-color: transparent;
}
.navbar-links{
    display: flex;
    flex-direction: column;
    text-align: right;
    font-family: "NTR", sans-serif;
    line-height: 35px;
    font-size: 18px;
    font-weight: bold;
}
.navbar-icons{
    padding-top: 42px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 8px;
}
a{
    color: var(--green-bright);
    text-decoration: none;
}
a:hover{
    color: var(--lightest-slate);
}